.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.speech-bubble {
  background-color: #efefef;
  border-radius: 10px;
  padding: 19px;
  max-width: 215px;
  text-align: left;
  padding-bottom: 1%;
}

img {
  border-radius: 10px;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.carousel-container {
  position: relative;
  max-width: 55%;
}

.carousel-container .carousel .slide {
  background-color: white;
  
}
.rcw-conversation-container > .rcw-header {
  background-color: #133f0f;
}

.rcw-launcher {
  background-color: #133f0f;
}

.rcw-close-button{
  background-color:#133f0f
}
.quick-button:active {
  background-color:#133f0f
}
.quick-button {
  border:2px solid #133f0f
}
.rcw-message-text{
  background-color:#133f0f
};
.quick-button:active{
  background:#133f0f
}
.rcw-client .rcw-message-text {
  background:#6ebd67a1
}